import  React from 'react'
import {makeStyles} from '@material-ui/core/styles';
import  {Avatar,Box,Typography} from '@material-ui/core';
import  {Link} from  'react-router-dom';
const useStyle=makeStyles({
    
    colorTypo:{
        color:"white",
        fontSize:"16px"
    }


});
export default  ({informacion})=>{
   
const classes=useStyle();
  return (
   <Link to="#" style={{textDecoration:"none"}}>
        <Box
           
            
            display="flex"
            alignItems="center"
        >
            <Avatar src="https://i.pinimg.com/originals/1a/9a/f6/1a9af61c7d87345d9676e0f57f7bb97f.gif"></Avatar>

            <Box flexGrow={1}>
                <Typography align="center" className={classes.colorTypo}>{informacion.email}</Typography>
                <Typography align="center" className={classes.colorTypo}>{informacion.msj}</Typography>
            </Box>

        </Box>
    </Link>
)};

